import React, { useState, useEffect } from "react";
import { createRoot } from 'react-dom/client';
import { csv } from "d3";
import DeliveryTrajectoryMap from './DeliveryTrajectoryMap';
import PieChart from './PieChart';
import 'leaflet/dist/leaflet.css';
import MatrixCorr from './corrMatrix'

let rawCsv = '/train.csv';

function useData(csvPath, setOrderIds, setSelectedOrderId){
    const [dataAll, setData] = useState(null);

    useEffect(() => {
        csv(csvPath).then(data => {
            const uniqueOrderIds = new Set(); // Use a Set to store unique order IDs

            data.forEach(d => {
                d.SourceLatitude = +d.SourceLatitude;
                d.SourceLongitude = +d.SourceLongitude;
                d.DestLatitude = +d.DestLatitude;
                d.DestLongitude = +d.DestLongitude;
                uniqueOrderIds.add(d.order_id); // Assume your data has an 'order_id' field
            });

            const orderIdsArray = [...uniqueOrderIds];
            setData(data);
            setOrderIds(orderIdsArray);
            if (orderIdsArray.length > 0) {
                setSelectedOrderId(orderIdsArray[0]); // Set the first order ID as the default selected one
            }
        });
    }, [csvPath, setOrderIds, setSelectedOrderId]);

    return dataAll;
}

function GojekRides(){
    const [orderIds, setOrderIds] = useState([]);
    const [selectedOrderId, setSelectedOrderId] = useState('');
    const [selectedCategory, setSelectedCategory] = useState('');
    const mapContainerWidth = '100%'; // This will make the map take the full width of its parent container
    const mapContainerHeight = '500px';
    const rawData = useData(rawCsv, setOrderIds, setSelectedOrderId);

    let orderTrajectory = rawData ? rawData.filter(d => d.order_id === selectedOrderId) : [];

    const handleOrderChange = (event) => {
        setSelectedOrderId(event.target.value);
    };

    return (
        <div>
            <h1>Information visualization dashboard</h1>
            <div className="mainView">
                <div style={{ maxWidth: '1200px', margin: '0 auto' }}>
                    <h2>Correlation Matrix</h2>
                    <MatrixCorr width={900} height={800} />
                </div>

                <div style={{ maxWidth: '1200px', margin: '0 auto' }}>
                    <h2>Map View</h2>
                    <select value={selectedOrderId} onChange={handleOrderChange}>
                        {orderIds.map(orderId => (
                            <option key={orderId} value={orderId}>
                                Order {orderId}
                            </option>
                        ))}
                    </select>
                    <div id="trajectory" style={{ 
                        width: mapContainerWidth, 
                        height: mapContainerHeight, 
                        margin: '10px 0'
                    }}>
                        <DeliveryTrajectoryMap 
                            height={mapContainerHeight} 
                            width={mapContainerWidth} 
                            data={orderTrajectory}
                            selectedOrderId={selectedOrderId}
                            setSelectedCategory={setSelectedCategory} 
                        />
                    </div>
                </div>

                <div style={{ maxWidth: '600px', margin: '0 auto' }}>
                    <h2>Pie Chart</h2>
                    <PieChart selectedCategory={selectedCategory} />
                </div>
            </div>
        </div>
    );
}

const rootElement = document.getElementById('root');
const root = createRoot(rootElement);
root.render(<GojekRides />);
