import React, { useEffect } from 'react';   
import { MapContainer, TileLayer, Polyline, Marker, Popup } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';

import markerIcon2x from 'leaflet/dist/images/marker-icon-2x.png';
import markerIcon from 'leaflet/dist/images/marker-icon.png';
import markerShadow from 'leaflet/dist/images/marker-shadow.png';

delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
    iconRetinaUrl: markerIcon2x,
    iconUrl: markerIcon,
    shadowUrl: markerShadow,
});

function DeliveryTrajectoryMap({ width, height, data, selectedOrderId, setSelectedCategory }) {
    useEffect(() => {
        const selectedData = data.find(d => d.order_id === selectedOrderId);
        if (selectedData) {
            const category = selectedData.label === '1' ? 'Real' : 'Fake';
            setSelectedCategory(category);
        }
    }, [selectedOrderId, data, setSelectedCategory]);

    if (!data || data.length === 0) {
        return <div>Loading map...</div>;
    }

    const trajectory = data.map(point => [point.latitude, point.longitude]);
    const center = [data[0].latitude, data[0].longitude];

    return (
        <div style={{ width: width, height: height }}>
            <MapContainer center={center} zoom={13} style={{ height: '100%', width: '100%' }}>
                <TileLayer
                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    attribution='&copy; OpenStreetMap contributors'
                />
                <Polyline color="blue" positions={trajectory} />
                {data.map((point, idx) => (
                    <Marker key={idx} position={[point.latitude, point.longitude]}>
                        <Popup>
                            Order ID: {point.order_id}<br />
                            Time: {point.date} {point.hour}:{point.seconds}
                        </Popup>
                    </Marker>
                ))}
            </MapContainer>
        </div>
    );
}

export default DeliveryTrajectoryMap;