import React, { useEffect, useState } from 'react';
import * as d3 from 'd3';

const PieChart = ({ selectedCategory }) => {
    const [data, setData] = useState([]);
    const [filter, setFilter] = useState("All");

    useEffect(() => {
        const processData = (data, filter) => {
            const filteredData = filter === "All" ? data : data.filter(d => d[filter] === '1');
            const counts = { real: 0, fake: 0 };
            filteredData.forEach(d => {
                if (d.label === '1') {
                    counts.real += 1;
                } else if (d.label === '0') {
                    counts.fake += 1;
                }
            });
            return [
                { label: 'Real GPS', value: counts.real, color: "#c6878f" },
                { label: 'Fake GPS', value: counts.fake, color: "#253d5b" }
            ];
        };

        d3.csv('/data.csv').then(data => {
            setData(processData(data, filter));
        });
    }, [filter]);

    const blinkAnimation = `
      @keyframes blink {
        0% { fill: #969696; }
        50% { fill: ${selectedCategory === 'Real' ? "#c6878f" : "#253d5b"}; }
        100% { fill: #969696; }
      }
    `;

    const width = 400;
    const height = 400;
    const radius = Math.min(width, height) / 2;
    const pie = d3.pie().value(d => d.value);
    const pieData = pie(data);
    const arc = d3.arc().innerRadius(0).outerRadius(radius);
    const labelArc = d3.arc().innerRadius(radius - 55).outerRadius(radius - 55);

    return (
        <div>
            <style>{blinkAnimation}</style>
            <select onChange={(e) => setFilter(e.target.value)}>
                <option value="All">All</option>
                <option value="GO_FOOD">GO_FOOD</option>
                <option value="GO_RIDE">GO_RIDE</option>
            </select>

            <svg width={width} height={height}>
                <g transform={`translate(${width / 2}, ${height / 2})`}>
                    {pieData.map((d, i) => (
                        <g key={i}>
                            <path d={arc(d)}
                                  fill={d.data.color}
                                  style={d.data.label.includes(selectedCategory) ? { animation: 'blink 1s linear infinite' } : {}}
                            />
                            <text
                                transform={`translate(${labelArc.centroid(d)})`}
                                dy=".35em"
                                textAnchor="middle"
                            >
                                {`${d.data.label} (${d.data.value})`}
                            </text>
                        </g>
                    ))}
                </g>
            </svg>
        </div>
    );
};

export default PieChart;
