import React, { useEffect, useRef } from "react";
import * as d3 from "d3";

function MatrixCorr(props) {
  const chartRef = useRef(null);

  useEffect(() => {
    // Adjust margins if needed based on the container size or via media queries
    const margin = { top: 200, right: 50, bottom: 10, left: 200 };
    const width = props.width - margin.left - margin.right;
    const height = props.height - margin.top - margin.bottom;

    const fetchData = async () => {
      try {
        const response = await fetch("./corr_matrix.csv");
        const csvData = await response.text();

        const svg = d3
          .select(chartRef.current)
          .append("svg")
          .attr("width", width + margin.left + margin.right)
          .attr("height", height + margin.top + margin.bottom)
          .append("g")
          .attr("transform", `translate(${margin.left},${margin.top})`);

        const data = d3.csvParse(csvData);
        debugger;
        const convertedData = [];
        data.forEach((item) => {
          const group = item[""];
          Object.keys(item).forEach((key) => {
            if (key !== "") {
              convertedData.push({
                group: group,
                variable: key,
                value: item[key] ? parseFloat(item[key]) : 0,
              });
            }
          });
        });
        const myGroups = [...new Set(convertedData.map((item) => item.group))];
        const myVars = [...new Set(convertedData.map((item) => item.variable))];

        const x = d3
          .scaleBand()
          .range([0, width])
          .domain(myGroups)
          .padding(0.01);
        svg
          .append("g")
          .attr("transform", `translate(0, 0)`)
          .call(d3.axisTop(x))
          .selectAll("text")
          .attr("dy", "1em")
          .attr("dx", "1.5em")
          .style("text-anchor", "start")
          .attr("transform", "rotate(-90)");

        const y = d3
          .scaleBand()
          .range([height, 0])
          .domain(myVars)
          .padding(0.01);
        svg.append("g").call(d3.axisLeft(y));
        debugger;
        const myColor = d3
          .scaleLinear()
          .range(["white", "#69b3a2"])
          .domain(d3.extent(convertedData, (d) => d.value));

        const defs = svg.append("defs");

        const linearGradientMap = defs
          .append("linearGradient")
          .attr("id", "color-map-gradient")
          .attr("x1", "0%")
          .attr("x2", "0%")
          .attr("y1", "0%")
          .attr("y2", "100%");

        const colorStopsMap = d3.range(1.01, -0.01, -0.1);

        colorStopsMap.forEach((stop) => {
          linearGradientMap
            .append("stop")
            .attr("offset", `${(1 - stop) * 100}%`)
            .attr("stop-color", myColor(stop * myColor.domain()[1]))
            .attr("stop-opacity", 1);
        });

        const colorMapRect = svg
          .append("rect")
          .attr("x", width + 10)
          .attr("y", 50)
          .attr("width", 20)
          .attr("height", height / 3)
          .style("fill", "url(#color-map-gradient)");

        const labelPositions = [1, 0.8, 0.6, 0.4, 0.2, 0];

        labelPositions.forEach((position) => {
          const value =
            position * (myColor.domain()[1] - myColor.domain()[0]) +
            myColor.domain()[0];
          svg
            .append("text")
            .attr("x", width + 40)
            .attr("y", 50 + (height / 3) * (1 - position))
            .text(value.toFixed(2))
            .attr("alignment-baseline", "middle")
            .attr("font-size", "0.8em");
        });

        const indicatorLine = svg
          .append("line")
          .attr("x1", width + 10)
          .attr("y1", 50)
          .attr("x2", width + 30)
          .attr("y2", 50)
          .attr("stroke", "red")
          .attr("stroke-width", 2)
          .style("display", "none");

        const mouseover = function (event, d) {
          const horizontalRect = svg
            .append("rect")
            .attr("class", "horizontalRect")
            .attr("x", 0)
            .attr("y", parseFloat(d3.select(this).attr("y")))
            .attr("width", width)
            .attr("height", parseFloat(d3.select(this).attr("height")))
            .style("fill", "none")
            .style("stroke", "black")
            .style("stroke-width", "0.2em");
          const verticalRect = svg
            .append("rect")
            .attr("class", "verticalRect")
            .attr("x", parseFloat(d3.select(this).attr("x")))
            .attr("y", 0)
            .attr("width", parseFloat(d3.select(this).attr("width")))
            .attr("height", height)
            .style("fill", "none")
            .style("stroke", "black")
            .style("stroke-width", "0.2em");

          const xPos = width + 10;
          let yPos;
          if (d.value >= 0) {
            yPos = 50 + (height / 3) * (1 - d.value / myColor.domain()[1]);
          } else {
            yPos =
              50 +
              (height / 3) *
                (1 -
                  (d.value - myColor.domain()[0]) /
                    (myColor.domain()[1] - myColor.domain()[0]));
          }
          indicatorLine
            .attr("x1", xPos)
            .attr("y1", yPos)
            .attr("x2", xPos + 20)
            .attr("y2", yPos);
          indicatorLine.style("display", "block");
        };

        const mouseleave = function (d) {
          svg.select(".horizontalRect").remove();
          svg.select(".verticalRect").remove();
          indicatorLine.style("display", "none");
        };

        svg
          .selectAll()
          .data(convertedData, function (d) {
            return d.group + ":" + d.variable;
          })
          .enter()
          .append("rect")
          .attr("x", function (d) {
            return x(d.group);
          })
          .attr("y", function (d) {
            return y(d.variable);
          })
          .attr("width", x.bandwidth())
          .attr("height", y.bandwidth())
          .style("fill", function (d) {
            return myColor(d.value);
          })
          .on("mouseover", mouseover)
          .on("mouseleave", mouseleave);
      } catch (error) {
        console.error("Error fetching or parsing CSV:", error);
      }
    };

    fetchData();
  }, []);

  return (
    <div 
      ref={chartRef} 
      id="my_dataviz" 
      style={{
        overflow: 'auto', // Enable both horizontal and vertical scrolling
        maxHeight: '500px', // Set a maximum height
        maxWidth: '100%', // Set a maximum width to 100% of the parent container
      }}
    ></div>
  );
}
export default MatrixCorr;